<template>
  <div class="column column-1 card-list">
  	 	<div class="title">
  	 		<span class="titletext fl"><i class="iconfont">&#xe62e;</i>美拍推荐</span>
			   <router-link to="meidiy/mtlist?name=meiP">
						<a href="javascript:;" class="more fr">更多<i class="iconfont">&#xe65f;</i></a>
			   </router-link>
  	 		
  	 	</div>
  	 	<div class="con-list">
  	 		<ul class="clearfix">
				   <li v-for="(item,index) of Mdata" :key="index">
					<router-link :to="'/meidiy/detail?id='+index+'&deName=meiP'">
						<div class="photo-card">
							<a href="#" class="img" style="background-image: url(../../static/images/img5.jpg);"><img :src="item.img"/></a>
							<p class="txt"><a href="#">{{item.title}}</a></p>
							<div class="info clearfix">
								<span class="author"><img :src="item.auimg">{{item.author}}</span>
								<span class="zan"><i class="iconfont">&#xe600;</i>{{item.love}}</span>
								<span class="collect"><i class="iconfont">&#xe605;</i>{{item.star}}</span>
							</div>
						</div>
					</router-link>
  	 			</li>
  	 		</ul>
  	 	</div>
  	 </div>
</template>
<script>
export default {
		data(){
		return{
			Mdata:[]
		}
	},
	mounted(){
		let data=this.$store.state.dataIndex.meiP;
		this.Mdata=data.slice(0,2);
	}
}
</script>

<style>
    .column-1 .title{border-bottom: 1px solid #fa669a; }
    .column-1 .titletext{ color: #fa669a;}
</style>



